<template>
  <div class="col s12 m6">
    <div>
      <div class="page-subtitle">
        <h4>Создать</h4>
      </div>

      <form @submit.prevent="submitHandler">
        <div class="input-field">
          <input
            id="name"
            type="text"
            v-model="state.title"
            :class="{ invalid: v$.title.$errors.length }"
          />
          <label for="name">Название</label>
          <span class="helper-text invalid" v-if="v$.title.$errors.length"
            >Введите название категории</span
          >
        </div>

        <div class="input-field">
          <input
            id="limit"
            type="number"
            v-model.number="state.limit"
            :class="{ invalid: v$.limit.$errors.length }"
          />
          <label for="limit">Лимит</label>
          <span class="helper-text invalid" v-if="v$.limit.$errors.length"
            >Минимальная величина {{ v$.limit.minValue.$params.min }}</span
          >
        </div>

        <button class="btn waves-effect waves-light" type="submit">
          Создать
          <i class="material-icons right">send</i>
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import useVuelidate from "@vuelidate/core";
import { required, minValue } from "@vuelidate/validators";
import { reactive } from "@vue/reactivity";
export default {
  data() {
    return {
      title: "",
      limit: 50,
    };
  },

  setup() {
    const state = reactive({
      title: "",
      limit: 50,
    });
    const rules = {
      title: { required },
      limit: { required, minValue: minValue(50) },
    };
    const v$ = useVuelidate(rules, state);
    return { state, v$ };
  },

  mounted() {
    M.updateTextFields();
  },

  methods: {
    async submitHandler() {
      if (this.v$.$invalid) {
        this.v$.$touch();
        return;
      }

      await this.$store.dispatch("createCategoty", {
        title: this.state.title,
        limit: this.state.limit,
      });
    },
  },
};
</script>